.footer {
	$this: &;
	padding: 92px 0 70px 0;
	font-size: 23px;
	font-weight: 100;
	line-height: 31.52px;
	color: #fff;
	background-color: $text-color;

	&__top {
		display: flex;
		margin-bottom: 75px;

		@include r(1300) {
			flex-wrap: wrap; }

		@include md {
			margin-bottom: 15px; } }

	&__item {
		width: 25%;

		@include r(1300) {
			width: 33.33%; }

		@include r(1000) {
			width: 50%; }

		@include sm {
			width: 100%;
			margin-bottom: 40px; }

		&--menu {
			padding-left: 78px;

			@include r(1400) {
				padding-left: 20px; } }

		&--awards {
			padding-left: 65px;

			@include r(1400) {
				padding-left: 20px; } }

		&--social {
			width: 20%;
			padding-left: 40px;
			padding-right: 50px;

			@include r(1400) {
				padding-left: 20px; }

			@include r(1000) {
				width: 100%;
				margin-top: 50px; }

			@include sm {
				margin-top: 0; } }

		&--info {
			width: 30%;
			padding-left: 45px;

			@include r(1400) {
				padding-left: 20px; }

			@include r(1300) {
				width: 100%;
				display: flex;
				justify-content: space-between;
				margin-top: 50px; }

			@include r(1000) {
				width: 100%;
				flex-direction: column; }

			@include sm {
				margin-top: 0; } } }


	&__title {
		display: block;
		color: inherit;
		text-decoration: none;
		font-size: 23px;
		font-weight: normal; }

	&__link {
		display: block;
		color: inherit;
		text-decoration: none;
		transition: color $anim;

		&:hover {
			color: $bg-color; } }

	&__link-title {
		@include xs {
			display: block; } }

	&__menu {

		&--sub2 {

			#{$this}__menu-item {
				position: relative;
				margin-left: 19px;
				padding-left: 11px;

				&::before {
					content: "";
					position: absolute;
					top: 50%;
					left: 0;
					transform: translate(0, -50%);
					width: 6px;
					height: 1px;
					background-color: #fff; } } } }

	&__menu-link {
		display: block;
		color: inherit;
		text-decoration: none;
		transition: color $anim;

		&:hover {
			color: $bg-color; } }

	&__social {
		margin-top: 28px;

		@include r(1000) {
			margin-top: 0; } }

	&__info-group {
		margin-bottom: 35px;

		&:last-child {
			margin-bottom: 0; } } }
