.hero {
	$this: &;
	position: relative;
	color: #FFF;
	background-color: $text-color;
	background-repeat: no-repeat;
	background-size: 500px;
	background-position: right center;
	background-size: auto 100%;
	background-position: right center;

	&__container {
		padding: 194px 0 124px 0;

		@include r(1550) {
			padding-top: 150px;
			padding-bottom: 150px; }

		@include lg {
			padding-top: 70px;
			padding-bottom: 70px; } }

	&__descr {
		display: block; }

	&__descr-additional {
		display: block; }

	&__wrap {
		width: 52%;

		@include r(875) {
			width: 100%; } }

	&__text {
		margin-bottom: 46px;
		font-size: 63px;
		font-weight: 400;
		line-height: 68.78px;

		@include r(1340) {
			font-size: 45px;
			line-height: 1.4; }

		@include md {
			font-size: 37px;
			line-height: 1.3; }

		@include sm {
			font-size: 30px;
			line-height: 1.2; } }

	&__descr-additional {
		font-weight: 100; }

	&__title {
		font-size: 23px;
		font-weight: 300;
		line-height: 31.52px; }

	&__btn {
		margin-top: 39px;
		padding: 8.5px 37px; }

	&__contacts {
		display: block;
		margin-top: 111px;
		font-size: 23px;
		font-weight: 32px; }

	&__contacts-list {
		display: flex;
		align-items: center;

		@include xs {
			align-items: flex-start;
			flex-direction: column; } }

	&__term {
		min-width: 130px; }

	&__value {
		display: block;
		color: $bg-color; }


	&__value-link {
		display: block;
		text-decoration: none;
		color: inherit; }

	&--default {
		position: relative;
		overflow: hidden;
		background-attachment: fixed;
		box-shadow: 0 19px 44px rgba(0, 0, 0, 0.36);

		#{$this}__container {
			padding-top: 90px;
			padding-bottom: 90px; }

		#{$this}__text {
			position: relative;
			z-index: 2;
			margin-bottom: 36px;

			&::before {
				content: "";
				position: absolute;
				top: 0;
				left: 12%;
				width: 450px;
				height: 0px;
				border-radius: 50%;
				background: rgba(120, 120, 120, 0);
				box-shadow: 161px 123px 304px 58px #000;
				z-index: -1;

				@include r(1550) {
					display: none; } } } } }
