.title-sect {
	color: $text-color;
	font-size: 42px;
	line-height: 52.08px;

	@include xs {
		font-size: 32px;
		line-height: 1.2; }


	&__main {
		display: block;
		text-decoration: none;
		color: inherit;
		font-size: inherit;
		font-weight: 400;
		line-height: inherit; }

	&__additional {
		display: block;
		margin-bottom: 90px;
		text-decoration: none;
		color: inherit;
		font-size: inherit;
		font-weight: 300;
		line-height: inherit;

		@include md {
			margin-bottom: 65px; } } }

