.common-sect {
	padding: 91px 0;

	@include xs {
		padding: 45px 0; }

	&__solutions {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		margin-bottom: 16px;
		padding: 0 50px;

		@include md {
			padding: 0; } }

	&__info {
		margin-top: 19px;
		margin-bottom: 130px; }

	&__title {
		margin-bottom: 63px;
		text-align: center; }

	&__services {
		display: flex;
		flex-wrap: wrap;
		padding: 48px 0px;

		@include md {
			padding: 10px 0; } }

	&__services-item {
		width: 50%;
		margin-bottom: 30px;

		&:nth-child(even) {
			padding-left: 11px; }

		@include md {
			width: 100%;
			padding-left: 0; } }

	&__reviews {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-left: -48px;
		margin-right: -48px;

		@include md {
			margin-left: 0px;
			margin-right: 0px;
			justify-content: center; } }

	&__btn {
		margin: 0 auto;
		text-align: center; }

	&__logos {
		padding-top: 50px;
		padding-bottom: 50px; }

	&--background {
		background-color: #f5f5f5; } }
