.social {
	display: flex;
	flex-flow: row wrap;
	justify-content: space-between;
	align-items: center;

	@include r(1000) {
		justify-content: flex-start; }

	&__item {
		margin-right: 5px;
		margin-bottom: 7px; }

	&__link {
		width: 40px;
		height: 40px;
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: #fff;
		border-radius: 50%;

		svg {
			display: inline-block;
			font-size: 16px;
			width: 1em;
			height: 1em;
			fill: currentColor;
			color: $text-color; }

		&:hover {
			svg {
				color: $bg-color; } }



		.icon-twit {
			width: 2em; }

		.icon-youtube {} } }
