.main-nav.is-open .main-nav__trigger-line::before, .main-nav.is-open .main-nav__trigger-line::after {
  top: 0;
  transition: top .3s,transform .3s .5s;
}

.main-nav.is-open .main-nav__trigger-line::before {
  transform: rotate3d(0, 0, 1, 45deg);
}

.main-nav.is-open .main-nav__trigger-line::after {
  transform: rotate3d(0, 0, 1, -45deg);
}

