.language {
	display: flex;
	flex-flow: row wrap;
	align-items: center;
	padding-left: 75px;
	color: #fff;

	&__list {
		display: flex;
		flex-flow: row wrap;
		align-items: center;
		padding-left: 10px;
		color: inherit; }

	&__item {

		&:not(:last-child) {
			padding-right: 4px;

			&::after {
				content: "/";
				display: inline-block;
				font-size: 18px;
				color: #fff; } } }

	&__ico {
		display: inline-block;
		margin-right: 10px;

		svg {
			width: 35px;
			height: 35px;
			display: inline-block;
			vertical-align: middle;
			color: #fff;
			fill: #fff; } }

	&__link {
		display: inline-block;
		color: inherit;
		text-decoration: none;

		&.is-active {
			font-weight: 700;
			color: $bg-color; }

		&:hover {
			color: $bg-color; } } }

