.slick-slide {
	position: relative;

	&::before {
		content: "";
		position: absolute;
		left: 0;
		top: 50%;
		transform: translate(0, -50%);
		width: 3px;
		height: 87px;
		background-color: $bg-color; }

	&:last-child {
		&::after {
			content: "";
			position: absolute;
			right: 0;
			top: 50%;
			transform: translate(0, -50%);
			width: 3px;
			height: 87px;
			background-color: $bg-color; } } }
