.services {

	&__title {
		margin-bottom: 55px;
		font-size: 31px;
		line-height: 1.4;
		font-weight: 700; }

	&__item {
		display: flex;
		margin-bottom: 64px; }

	&__item-title {
		display: block;
		font-size: 23px;
		line-height: 1;
		font-weight: 700;
		text-decoration: none; }

	&__item-text {
		display: block;
		font-size: 23px;
		line-height: 1.2;
		text-decoration: none; }

	&__ico {
		display: inline-block;
		margin-right: 37px;
		min-width: 65px;

		.icon {
			width: 60px;
			height: 60px;
			display: inline-block;
			vertical-align: middle;
			fill: transparent;
			stroke: #000;

			&.icon-consultancy {
				width: 65px;
				height: 32px; }

			&.icon-busines {
				width: 62px;
				height: 73px; }

			&.icon-education {
				width: 73px;
				height: 53px; }

			&.icon-support {
				width: 66px;
				height: 59px; }

			&.icon-self {
				width: 65px;
				height: 58px; }

			&.icon-outsorcing {
				width: 71px;
				height: 86px; } } } }

