.main-nav {
	$this: &;

	&.is-open {
		#{$this}__menu-wrap {
			display: block; }

		#{$this}__trigger-line {
			background: 0 0;

			&::before,
			&::after {
				top: 0;
				transition: top .3s,transform .3s .5s; }

			&::before {
				transform: rotate3d(0,0,1,45deg); }

			&::after {
				transform: rotate3d(0,0,1,-45deg); } } }

	&__menu-wrap {
		@include lg {
			display: none;
			position: fixed;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			z-index: 20;
			min-width: 320px;
			padding-top: 15px;
			padding-right: 16px;
			padding-left: 16px;
			overflow-y: auto;
			background-color: $text-color; } }

	&__list {
		display: flex;
		justify-content: space-between;

		@include lg {
			flex-direction: column; }

		&--sub2 {
			display: none;
			position: absolute;
			top: 100%;
			left: 0;
			z-index: 10;
			width: 340px;
			padding: 12px 0;
			background-color: $bg-color;
			box-shadow: 0 0 40px rgba(0,0,0,.54);
			border-radius: 5px;

			&.is-open {
				display: block;

				@include lg {
					position: relative;
					box-shadow: none;
					width: 100%; } }

			#{$this}__link {
				padding: 8px 25px;
				font-weight: 400; } } }

	&__menu-wrap > #{$this}__list > #{$this}__item:not(:last-child):before {
		content: "";
		position: absolute;
		top: 50%;
		right: 0;
		height: 18px;
		width: 1px;
		transform: translate(0, -50%);
		background-color: #fff;

		@include lg {
			display: none; } }

	&__item {
		position: relative;
		white-space: nowrap;

		&:hover {
			#{$this}__list--sub2 {
				@include rmin(1430) {
					display: block; } }

			#{$this}__link::after {
				border-color: $bg-color transparent transparent; } }

		&--dropdown {
			color: red;

			& > #{$this}__link {

				&::after {
					content: "";
					display: inline-block;
					width: 0;
					height: 0;
					margin-left: 12px;
					vertical-align: middle;
					border-width: 6px 4px 0;
					border-style: solid;
					border-color: #ffffff transparent transparent; } } } }

	&__link {
		position: relative;
		display: block;
		padding: 15px 23px;
		color: #fff;
		font-size: 17px;
		font-weight: 300;
		line-height: 22.92px;
		text-decoration: none;
		cursor: pointer;

		&:hover {
			text-decoration: underline; }

		&.is-active {
			color: $bg-color; } }

	&__trigger {
		display: none;
		position: relative;
		top: 0;
		right: 0;
		z-index: 25;
		justify-content: center;
		align-items: center;
		box-sizing: content-box;
		width: 30px;
		height: 30px;
		padding-left: 36px;
		background: 0 0;
		border: 0;
		user-select: none;
		cursor: pointer;

		@include lg {
			display: flex; }

		& > span {
			position: relative;
			display: inline-block;
			width: 30px;
			height: 3px;
			vertical-align: middle;
			background: #fff;
			transition: background .3s .5s;

			&::after,
			&::before {
				content: "";
				position: absolute;
				left: 0;
				width: 100%;
				height: 3px;
				background: #fff;
				-webkit-transform-origin: 50% 50%;
				transform-origin: 50% 50%;
				transition: top .3s .3s, transform .3s; }

			&::after {
				top: -10px; }

			&::before {
				top: 10px; } } } }
