.reviews {

	&__item {
		width: 33.33%;
		padding: 0 48px;
		text-align: center;

		@include r(1300) {
			padding: 0 10px; }

		@include md {
			width: 50%;
			margin-bottom: 35px; }

		@include sm {
			width: 100%; } }


	&__media {
		width: 125px;
		height: 125px;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 52px;
		background-repeat: no-repeat;
		background-position: center;
		background-size: 100%;
		border-radius: 50%; }

	&__text {
		position: relative;
		margin-bottom: 30px;
		padding-bottom: 30px;
		color: $text-color;
		font-size: 23px;
		line-height: 28.13px;
		font-weight: 400;

		&:after {
			content: "";
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translate(-50%);
			width: 40px;
			height: 1px;
			background-color: $bg-color; } }

	&__name {
		display: block;
		text-decoration: none;
		font-size: 17px;
		line-height: 28px;
		font-weight: 300; }

	&__link {
		display: block;
		font-size: 17px;
		font-weight: 400;
		color: $bg-color;
		text-decoration: none; }

	&__blockquote {
		padding: 0 16px; }

	&__person {
		font-style: normal; } }
