@include font-weight('azosans', azosans-thin, 100);
@include font-weight('azosans', azosans-light, 300);
@include font-weight('azosans', azosans-regular, 400);

@mixin azosans-font {
	font-family: "azosans", sans-serif; }

body {
	background: #FFF;
	@include azosans-font;
	font-weight: 400;
	font-size: 16px;
	line-height: 1.4; }

.container {
	max-width: 1430px;
	margin: 0 auto;
	padding: 0 15px;

	&--padding {
		padding: 0 95px;

		@include md {
			padding: 0 55px; }

		@include xs {
			padding: 0 15px; } } }


