.header {
	position: relative;
	padding: 25px 20px;
	z-index: 9;
	// box-shadow: 0 0 40px rgba(0, 0, 0, 0.54)

	filter: drop-shadow(0 1px 11px rgba(0,0,0,.54));
	background-color: $text-color;

	@include sm {
		padding: 18px 20px; }

	&__wrap {
		display: flex;
		justify-content: space-between;
		align-items: baseline;
		max-width: 1500px;
		margin: 0 auto;

		@include sm {
			flex-wrap: wrap; } }

	&__nav {
		display: flex;
		align-items: center;
		margin-left: auto;

		@include lg {
			order: 2;
			margin-left: 0; } }

	&__nav-phone {
		@include sm {
			width: 100%;
			order: 2;
			margin-top: 10px; } } }
