.btn {
	display: inline-block;
	padding: 10px 47px;
	font-size: 23px;
	font-weight: 400;
	line-height: 28px;
	font-weight: 400;
	color: #fff;
	text-decoration: none;
	border-radius: 36px;
	background-color: $bg-color;
	user-select: none;
	transition: background-color $anim;

	&:hover,
	&:focus {
		background-color: #6eab0f; }

	&:active {
		color: #eee; }

	&__ico {
		margin-right: 10px;

		svg {
			width: 19px;
			height: 19px;
			fill: transparent;
			stroke: #fff;
			display: inline-block;
			vertical-align: middle; } }

	&--ico {
		padding: 7px 20px;
		font-size: 17px;
		font-weight: 400;
		line-height: 22.92px; } }
