.solutions {

	&__item {
		display: flex;
		width: 33.33%;
		margin-bottom: 67px;
		padding: 0 24px;

		@include r(1300) {
			width: 50%;
			padding: 0 10px; }

		@include r(800) {
			width: 100%; } }

	&__wrap {
		display: flex;
		flex-direction: column;
		overflow: hidden;
		box-shadow: 0 0 60px rgba(0, 0, 0, 0.15);
		border-radius: 12px;
		border: 1px solid rgba(24, 24, 24, 0.16); }

	&__media {
		margin-bottom: 45px; }

	&__content {
		display: flex;
		flex-direction: column;
		flex-grow: 1;
		align-items: center;
		padding-left: 33px;
		padding-right: 33px;
		padding-bottom: 30px;
		text-align: center; }

	&__title {
		display: block;
		margin-bottom: 9px;
		font-size: 29px;
		line-height: 1;
		color: $text-color;
		text-decoration: none; }

	&__descr {
		display: block;
		margin-bottom: 37px;
		font-size: 23px;
		line-height: 28px;
		color: $text-color;
		text-decoration: none; }

	&__img {
		width: 100%;
		height: 100%;
		object-fit: cover; }

	&__btn {
		margin-top: auto; } }
